body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	font-family: 'Lato', sans-serif;
	overflow-x: hidden;
}

.site-header {
	width: 100vw;
	height: 60vh;
	background: url(../../static/media/img1.e0a4a16b.jpg) center/cover;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	margin-bottom: 10vh
}


.input {
	-webkit-appearance: none;
	width: 50%;
	border: 0;
	font-family: inherit;
	padding: 12px 0;
	height: 48px;
	font-size: 2rem;
	font-weight: 400;
	color: #ffffff;
	background: none;
	border: 2px solid #c8ccd4;
	border-radius: 0;
	padding-left: 10px;
	transition: all 0.5s ease;
}

::-webkit-input-placeholder {
	color: #d8d8d8;
	transition: 0.3s;
}

::-ms-input-placeholder {
	color: #d8d8d8;
	transition: 0.3s;
}

::placeholder {
	color: #d8d8d8;
	transition: 0.3s;
}

.input:focus {
	background: rgba(0, 0, 0, 70%);
}

.submit-arrow {
	cursor: pointer;
	width: 50px;
	height: 50px;
	background: url(../../static/media/arrow.d01edd19.png) center/cover;
	border: none;
}

.no-result {
	font-size: 1.5rem;
	color: #5e5e5e;
	text-align: center;
	margin-top: 23vh;
}

.loading {
	width: 100px;
	margin-left: 45%;
	margin-top: 3%;
}

@media screen and (max-width: 992px) {
	.input {
		width: 80%;
		font-size: 1rem;
	}

	.site-header {
		width: 100vw;
		height: 50vh;
	}

	.loading {
		margin-left: 35%;
	}
}
.books-list {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
	list-style: none;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
}

.book-element {
	max-width: 20%;
	max-height: 20%;
	min-width: 20%;
	min-height: 20%;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	margin: 20px;
	padding: 15px;
	border: 1px solid rgb(231, 231, 231);
	box-shadow: 3px 3px 10px rgb(156, 156, 156)
}

.book-thumbnail {
	width: 200px;
	height: 200px;
	margin-bottom: 15px;
}

.book-details {
	margin: 5px;
	text-align: center;
}

.title {
	max-height: 30%;
	overflow-y: scroll;
	font-weight: 800;
	font-size: 1rem;
}

.author {
	font-size: 0.9rem;
	font-weight: 400;
}

.description {
	max-height: 30%;
	overflow-y: scroll;
	font-size: 0.8rem;
	font-weight: 500;
}


@media screen and (max-width: 1400px) {
	.book-element {
		max-width: 30%;
		max-height: 30%;
		min-width: 30%;
		min-height: 30%;
	}
}


@media screen and (max-width: 900px) {
	.books-list {
		display: -webkit-flex;
		display: flex;
		-webkit-flex-direction: column;
		        flex-direction: column;
		-webkit-justify-content: center;
		        justify-content: center;
		-webkit-align-items: center;
		        align-items: center;
	}

	.book-element {
		max-width: 90%;
		max-height: 90%;
		min-width: 90%;
		min-height: 90%;
	}
}
